<template>
    <v-form @submit.prevent="signupHandlerSubmit" ref="form" class="row">
        <div class="col-lg-6">
            <v-text-field
                v-model="signupUser.name"
                :label="$t('signup.namePlaceholder')"
                :loading="loadingForm"
                :color="theme.colors.brandPrimColor"
                light
                height="40"
                outlined
                append-icon="mdi-account-outline"  
                :rules="nameRules"
            ></v-text-field>
        </div>
        <div class="col-lg-6">
            <v-text-field
                v-model="signupUser.email"
                :label="$t('signup.emailPlaceholder')"
                :loading="loadingForm"
                :color="theme.colors.brandPrimColor"
                light
                height="40"
                outlined
                append-icon="mdi-email-outline"
                :rules="emailRules"
            ></v-text-field>
        </div>
        <div class="col-lg-6">
            <v-text-field
                v-model="signupUser.mobile"
                :label="$t('signup.mobilePlaceholder')"
                :loading="loadingForm"
                :color="theme.colors.brandPrimColor"
                light
                height="40"
                outlined
                append-icon="mdi-phone-outline"
                :rules="mobileRules"
                autocomplete
                
            ></v-text-field>
        </div>
        <div class="col-lg-6">
            <v-text-field
                v-model="signupUser.password"
                :append-icon="showPassword ? svg.visibility : svg.visibilityOff"
                :type="showPassword ? 'text' : 'password'"
                name="input-10-1"
                :label="$t('login.passPlaceholder')"
                :loading="loadingForm"
                @click:append="showPassword = !showPassword"
                :color="theme.colors.brandPrimColor"
                height="40"
                outlined
                autocomplete
                :rules="passRules"
            ></v-text-field>
        </div>
        <div class="col-lg-12">
            <v-text-field
                v-model="signupUser.bio"
                :label="$t('signup.bioPlaceholder')"
                :loading="loadingForm"
                :color="theme.colors.brandPrimColor"
                light
                height="40"
                outlined
                append-icon="mdi-account-badge-horizontal-outline"
                :rules="bioRules"
            ></v-text-field>
        </div>
        <div class="col-lg-12">
            <!-------------------------  FORM ERRORS  ------------------->
            <div v-if="errors" class="subtitle1 text-center text-capitalize red--text">
                <!-- <div v-for="(errorMessage, i) in errors" :key="i">
                    <p v-for="(Message, i) in errorMessage" :key="i">{{Message}}</p>
                </div> -->
                <p v-for="(errorMessage, i) in errors" :key="i">{{errorMessage}}</p>
                
                <!-- <span>Don't have an account? <router-link class="cyan--text" to="/signup">Sign Up Here</router-link></span> -->
            </div>
            <!-------------------------  END FORM ERRORS ------------------->
        </div>
        <div class="col-lg-12">
            <div class="mt-5">
                <v-btn type="submit" large :loading="loadingForm" :class="{ 'mr-4': locale == 'en' }" class="primBtn"  :color="theme.colors.brandPrimColor" elevation="0" dark>
                    {{$t('signup.signupBtn')}}
                </v-btn>
                <router-link class="v-btn secBtn mr-4" to="/login">{{$t('signup.loginBtn')}}</router-link>            
            </div>
        </div>



    </v-form>
</template>


<script>
// MIXINS
import { reset } from '@/mixins/mixins';
import i18n from '@/plugins/i18n';

// SVG ICONS
import { mdiEyeOutline, mdiEyeOffOutline  } from '@mdi/js';

//VUEX
import { mapGetters } from 'vuex';
import axios from 'axios';


export default {
    mixins: [reset],
     data() {
        return {
            showPassword: false,
            signupUser: {
                name:"",
                password:"",
                bio:"",
                mobile:"",
                email:"",
            },
            nameRules:[
                v => !!v || i18n.messages[this.locale].signup.errors.reqUserName,
            ],
            emailRules: [
                v => !!v || i18n.messages[this.locale].signup.errors.reqEmail,
                v => /.+@.+\..+/.test(v) || i18n.messages[this.locale].signup.errors.emailMustValid,
            ],
            
            mobileRules: [
                v => !!v || i18n.messages[this.locale].signup.errors.reqMobile,
                v => /^(009665|9665|\+9665|05|5)(5|0|3|6|4|9|1|8|7)([0-9]{7})$/.test(v) || i18n.messages[this.locale].signup.errors.mobileMustValid,
            ],
            passRules: [
                v => !!v || i18n.messages[this.locale].signup.errors.reqPass,
                v => (v && v.length >= 6) || i18n.t('signup.errors.passMoreThanNChars', {'n': '6'}),
            ],
            bioRules: [
                v => !!v || i18n.messages[this.locale].signup.errors.reqBio,
                v => (v && v.length >= 10) || i18n.t('signup.errors.bioMoreThanNChars', {'n': '10'}),
            ],
            svg: {
                visibility: mdiEyeOutline,
                visibilityOff: mdiEyeOffOutline,
            },
        }
    },
    methods: {
        signupHandlerSubmit() {
            let LocalOrgSlug = process.env.VUE_APP_ORG_SLUG;
            let full = window.location.host;
            let parts = full.split('.');
            let subDomain = parts[0];
            
            if (this.$refs.form.validate()) {
                this.$store.dispatch('SIGN_UP', {
                    "organization": LocalOrgSlug ? LocalOrgSlug : subDomain,
                    "name": this.signupUser.name,
                    "password": this.signupUser.password,
                    "bio":this.signupUser.bio,
                    "mobile":this.signupUser.mobile,
                    "email":this.signupUser.email,
                    "locale":this.locale,
                })
                .then(() => {
                    this.$router.push({name: 'login'});
                })
            }
        },
    },
    computed: {
        ...mapGetters(['errors', 'loadingForm', 'theme', 'locale'])
    },
    beforeDestroy() {
        this.$store.dispatch('CLEAR_ERROR')
    },
    mounted() {

        

    },
}
</script>
<style lang="scss" scoped>

    fieldset {
        display: none;
    }
</style>